
.skill-div {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: repeat(auto, 1fr);
  width: 75vw;
  gap: 10px;
  color: white;
  margin: auto;
  /* */

  /* border: 1px solid white; */
}
.skill-title {
  font-size: 70px;
  color: var(--title-color);
  margin-top: 200px;
}
#skills {
  color: white;
}
.skill-div > div {
  /* border: 1px solid white; */
  padding: 10px;
}
.skill-div > div > img {
  width: 80px;
  height: 80px;
  color: white;
  background-color: beige;
  border-radius: 20px;
  padding: 20px;
}
.skill-div > div > img:hover{
  /* background-color: var(--title-color); */
  transform: scale(1.2);
  /* transform: rotate(90%); */
  box-shadow: 1px solid red;
  /* transition: cubic-bezier(0.075, 0.82, 0.165, 1); */
}
@media screen and (max-width: 480px) {
  .skill-div {
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(auto, 1fr);
    width: 90vw;
    gap: 0px;
  }
  .skill-title {
    font-size: 40px;
    color: var(--title-color);
  }

  .skill-div > div > img {
    width: 45px;
    height: 45px;
    color: white;
    /* background-color: beige; */
    border-radius: 10px;
  }
  .skill-div > div > h3 {
    font-size: 12px;
  }
  .skill-div > div {
    /* border: 1px solid white; */
    padding: 0px;
  }
}
